/* Backgrounds */
.background-primary { background-color: #ffffff; }

.background-secondary { background-color: #F5F5F5; }

/* Typography */
body { font-family: "Roboto Condensed", Verdana, Geneva, sans-serif; weight: normal; line-height: 28px; color: #444444; opacity: 100%; }

body h1 { font-family: Rubik, Verdana, Geneva, sans-serif; weight: normal; line-height: 1.25; color: #4A4A4A; opacity: 100%; }

body h2, body h3, body h4, body h5 { font-family: Rubik, Verdana, Geneva, sans-serif; weight: 500; line-height: 25px; color: #444444; opacity: 100%; }

body hr { height: 2px; color: #cccccc; opacity: 0.5; }

body #page-wrapper { margin-top: 60px; display: inline-block; width: 100%; }

@media (max-width: 767px) { body #page-wrapper { margin-top: 60px; } }

/* Responsive Font Styling */
@media (min-width: 768px) { body h1 { font-size: 32px; }
  body h2, body h3, body h4, body h5 { font-size: 21px; }
  body p { font-size: 16px; } }

@media (max-width: 767px) { body h1 { font-size: 21px; }
  body h2, body h3, body h4, body h5 { font-size: 18px; }
  body p { font-size: 16px; } }

